<template>
	<view class="chat">
		
		<view class="chat-detail">
			<!-- 帖子详情 -->
			<view class="my-card card">
				<!-- 头像，名称 ,删除按钮，收藏按钮 -->
				<view v-if="talk.user!=null" class="headr l_r">
					<view style="font-weight: 600;" class="left user-info">
						<image @click="gotoUserDetail(talk.user.userId)"  style="width: 80rpx;height: 80rpx;margin-right: 10px;" :src="setUrl(talk.user.avatar)"></image>
						<span>{{talk.user.nickName}}</span>
						 
					</view>
					<view class="right">
						<!-- <view  class="item"><span class="iconfont icon-shoucang"></span></view> -->
						<!-- <view @click="()=>{this.$refs.deleteDiscussPoput.open()}" style="margin-left: 8px;" class="item"><span class="iconfont icon-shanchu"></span></view> -->
					</view>
				</view>
				<!-- 图片，内容 -->
				<view @click="openInput(null)" class="card-text">
					<text selectable="true"  >{{talk.content}}</text>
					<view  class="card-image" v-if="talk.pictrueList!=null">
						<view  @tap.stop.prevent v-for="(item,index) in imageList" class="image-item" @click.stop="gotoShowImage(index)" >
							<image mode="aspectFill" :src="item"></image>
						</view>
					</view>
				</view>
				<!-- 页脚:时间，点赞，收藏，分享 -->
				<view class="card-footer l_r">
					<view style="margin-left: 10px;" @click="talkThumbs()" class="item">
						
						<image v-if="talk.thumbs" style="width: 20px;height: 20px;" src="../../static/images/icon/like-active.png"></image>
						<image v-else style="width: 20px;height: 20px;" src="../../static/images/icon/like.png"></image>									
						<span class="item-text " >点赞</span>
						<!-- <span :class="[talk.thumbs?'iconfont icon-dianzan1 icon-active':'iconfont icon-dianzan1']"></span>
						<span class="item-text " >{{talk.thumbsNum}}</span> -->
					</view>
					<!-- 收藏 -->
					<view @click="star()" class="item">
						<image v-if="talk.star" style="width: 20px;height: 20px;" src="../../static/images/icon/star-active.png"></image>
						<image v-else  style="margin-left: 8px;width: 20px;height: 20px;"  class="item"  src="../../static/images/icon/star.png"></image>
						<span class="item-text " >收藏</span>
					</view>
					<view @click="openInput(null)" class="item">
						<image style="margin-left: 8px;width: 20px;height: 20px;"  class="item"  src="../../static/images/icon/comment-active.png"></image>
						<span class="item-text " >评论</span>
					</view>
					<view style="margin-right: 10px;" @click="openShare(null)" class="item">
						<!-- <span class="iconfont icon-pinglun"></span> -->
						<image  style="width: 20px;height: 20px;"  src="../../static/images/icon/share.png"></image>
						<span class="item-text " >{{c_item.commentNum}}</span>
						<span class="item-text " >分享</span>
					</view>
					
					
				</view>
				<!-- 回复区 -->
				<view v-if="talk.thumbsList"  class="interactive-box" >
					<!-- 点赞区 -->
					<view class="top">
						<!-- <span class="iconfont icon-dibudaohanglan-"></span> -->
						<image style="width: 20px;height: 20px;margin-right: 8px;" src="../../static/images/icon/like-active.png"></image>
						
						<view  class="fabulous-list min-front">
							<view v-for="(item,index) in talk.thumbsList" class="user-info mg":key="index" >
								<image  class="box-size av-border" :src="setUrl(item.avatar)"></image>
								<span class="user_name" >{{item.nickName}}</span>
							</view>
						</view>
					</view>
					
					
				</view>
				
				
			</view>	
				
			<!-- 评论区 -->
			<view  class="chat-comment-content">
				<view class="l_r">
					<view class="left"><span>相关回复{{total}}条</span></view>
					<view class="right">
						<view @click="subscribe()" v-if="queryParams.orderType==1" style="display: flex;align-items: center;">
							<image style="margin-right: 4px;" class="icon" src="../../static/images/icon/sub.png"></image>
							<span>订阅</span> 
						</view>
						<!-- <view v-else style="display: flex;align-items: center;" class="item">
							<image style="margin-right: 4px;" class="icon" src="../../static/images/icon/fire.png"></image>
							<span>按热度</span>
						</view> -->
					</view>
				</view>
				<view  class="chat-comment-list">
					
					<view class="item" v-for="(item,index) in talkCommentList">
						<view v-if="isZZ(item.content)" style="padding: 10px 30rpx;" class="item">
							<myTransmferAccounts :money="item.content.substr(5)" :userId="item.user.userId" :avatarUrl="setUrl(item.user.avatar)" :zz_type='0' :dir='0' ></myTransmferAccounts>

						</view>
						
						<my-chat-comment v-else @removeTalkComment="removeTalkComment"  @openInput="openInput" :c_item="item"></my-chat-comment>
					</view>
				</view>
			</view>
			
		</view>
		
		<!-- 输入框弹窗 -->
		<bottomInput :placeholder='placeholder' :isOpenInput="isOpenInput" @closeInput="closeInput" @send="send"></bottomInput>
		
		<!-- 删除确认框 -->
		<my-poput ref="deleteDiscussPoput" @submitForm="deleteDiscuss" >
			<template slot="title">
				<span>删除</span>
			</template>
			<template slot="content">
				<span>确定删除该评论？</span>
			</template>
		</my-poput>
		
		<myLoad :status="status"></myLoad>
		
		<myShare ref="myShare"></myShare>
	</view>
	
	
	
	
</template>

<script>
	
	import myChatComment from "@/components/myChatComment/myChatComment.vue"
	import myPoput from "@/components/myPoput/myPoput.vue"
	import {getTalkById,talkThumbs,collectionTalk} from "@/api/talk.js"
	import {getTalkCommentPage,addTalkComment,subscribe} from "@/api/talkComment.js"
	import myShare from "@/components/myShare/myShare.vue"
	export default {
		name:"myChat",
		components:{
			myPoput,
			myChatComment,
			myShare
		},
		data() {
			return {
				placeholder:"请输入评论",
				talkId:"",
				talk:{},
				status:"more",
				parentId:null,
				// 查询参数
				queryParams: {
					talkId:"",
					pageNum: 1,
					pageSize: 5,
					orderType: 1,
				},
				//总条数
				total:0,
				isOpenInput:false,
				//回复的人的id
				userId:null,
				thumbsNum:0,
				thumbs:0,
				imageList:[],
				Height:0,
				KeyboardHeight:0,
				talkCommentList:[]
			
			};
		},
		// 自定义此页面的转发给好友(已经有全局的分享方法，此处会覆盖全局)
		onShareAppMessage(res) {
			return {
			  title:this.talk.content,
			  path: `/pages/chatDetail/chatDetail?id=${this.talk.id}`
			  // imageUrl: '/static/logo.png'
			}
		  },
		  //2.分享到朋友圈
		  onShareTimeline(res) {
			return {
				title:this.talk.content,
				path: `/pages/chatDetail/chatDetail?id=${this.talk.id}`
			}
		  },
		//下拉刷新
		onPullDownRefresh() {
			
			this.getTalkById();
			this.queryParams.pageNum = 1;
			this.talkCommentList = [];
			this.getTalkCommentList().then(()=>{uni.stopPullDownRefresh()})
		},
		//触底
		onReachBottom(){
			// console.log("触底了")
			this.handleCurrentChange();
		},
		onLoad(op) {
			this.talkId = op.id;
			this.getTalkById();
			this.queryParams.talkId = op.id;
			this.getTalkCommentList();
			
		},
		methods:{
			
			//用户详情
			gotoUserDetail(userId){
				uni.navigateTo({
					url:`/pages/userDetail/userDetail?userId=${userId}`
				})
			},	
			getTalkById(){
				const param = {
					talkId : this.talkId
				}		
				getTalkById(param).then(res=>{
					// console.log(res)
					this.imageList = []
					if(res.data.pictrueList!=null){
					
						res.data.pictrueList.forEach((item)=>{
							this.imageList.push(this.setUrl(item))
						})
					}
					this.talk = res.data;
				})
			},
			async getTalkCommentList(){
				//拿评论
				this.status = "loading"
				const res = await getTalkCommentPage(this.queryParams);
				
				this.talkCommentList = [...this.talkCommentList,...res.data.rows];
				
				this.total =  res.data.total
				
				if(!this.isMore())
					this.status = "nomore"
				else
					this.status = "more"
			},
			changeOrder(){
				this.queryParams.orderType^=1;
				this.queryParams.pageNum = 1;
				this.talkCommentList = [];
				this.getTalkCommentList();
			},
			//收藏
			star(){
				
				this.talk.star^=1;
				collectionTalk(this.talkId);
			},
			setUrl(url){
				return this.getUrl(url);
			},		
			//点赞
			talkThumbs(){
				this.talk.thumbs^=1;
				
				this.talk.thumbsNum = this.talk.thumbsNum + (this.talk.thumbs==1?1:-1)
				
				talkThumbs(this.talkId).then(res=>{
					
				}).catch(err=>{
					
				})
			},

			
			isMore(){
				if(this.queryParams.pageNum*this.queryParams.pageSize >= this.total){
					// console.log("无数据")
					return false;
				}
				return true;
			},
			//分页
			handleCurrentChange(){
				if(!this.isMore()){
					this.status = "nomore"
					return ;
				}
				this.queryParams.pageNum++;
				this.queryParams.pageSize;
				this.getTalkCommentList();
				
			},
			
			setUrl(url){
				return this.getUrl(url);
			},
			gotoShowImage(index){
				this.fullScreen(index);
			},
			//全屏预览图片函数方法
			fullScreen(i) {
				// console.log(i)
				uni.previewImage({
					urls: this.imageList, //需要预览的图片http链接列表，多张的时候，url直接写在后面就行了
					current: i, // 当前显示图片的http链接，默认是第一个
					success: function(res) {console.log("打开成功")},
					fail: function(res) {console.log("打开失败")},
					complete: function(res) {console.log("完成打开")},
				})
			},
	
			//点击发送信息回调
			send(val){
				//获取到val
				this.addTalkComment(val);
			},
			closeInput(){
				this.isOpenInput = false;
			},
			//向评论/帖子添加评论
			openInput(id){
				if(id==null){
					this.placeholder="请输入评论"
				}else{
					this.placeholder="回复"
				}
				//id==null 回复c_item
				//id!=null 回复c_item的评论
				this.parentId = id;
				this.isOpenInput=true;
			
				// //触发输入框
				// this.$store.commit("OPEN_INPUT");
			},		
			addTalkComment(val){
				if(this.stringUtils.isEmpty(val)){
					uni.showToast({
						title: "内容不能为空！",
						icon:"none",
						duration:850
					});
					return;
				}
					
				const param = {
					//评论的帖子id
					talkId:this.talkId,
					//被评论的人
					parentId:this.parentId,
					//评论的内容
					content:val
				}
				// console.log(param)
				addTalkComment(param).then((res)=>{
					this.talkCommentList = [
						res.data,
						...this.talkCommentList
					]
					this.total++
				}).catch(err=>{
					
				})
			},
			//分享
			openShare(){
				this.$refs.myShare.open();
			},
			removeTalkComment(id){
				 this.talkCommentList = this.talkCommentList.filter(item=>{return item.id != id})
				 --this.total;
			},
			isZZ(item){
				const regex = /^\&zz00\w+/;
				return regex.test(item)
			},
			/**
			 * 订阅消息
			 */
			subscribe(){
				wx.requestSubscribeMessage({
					tmplIds: ['w6J_8bSEtK_2Tnscws-HWKJ_SxG0ANCQIcOZwK-AsWw'],
					success : (res) => { 
						
						if(res['w6J_8bSEtK_2Tnscws-HWKJ_SxG0ANCQIcOZwK-AsWw'] == "accept") {
							subscribe(this.talkId);
							uni.showToast({
								title:"订阅成功",
								icon:'none'
							})
						}
							
					},
				
				})
			}
			
		
		}
	}
</script>

<style>
	/* 卡片内边距 */
.my-card{
	padding: 0 10px 10px 10px;
	/* margin: 30px 0; */
}

.chat-detail{
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}
.headr{
	padding-top: 5px;
}
.headr > .iconfont{

	margin-right: 20px;
}


/* 内容 */
.card-text{
	
	padding: 20px 10px;
	
}
/* 页脚 */
.card-footer{
	width: 100%;
	padding-bottom: 20px;
}

.card-footer  > .item {
	display: flex;
	align-items: center;
	
}

.card-footer .item .item-text{
	margin-left: 4px;
}
/* 回复区开始 */
.interactive-box .iconfont{
	margin-right: 5px;
	margin-top: 5px;
}
.interactive-box >.top,.interactive-box > .bottom{
	display: flex;
}
/* 点赞列表和聊天列表 */
.fabulous-list,.chat-list{
	
	overflow: hidden;
	display: flex;
	padding: 0px 5px 5px 5px;
	background-color: #f4f4f4;
	flex: 1;
}
/* 头像大小 */
.box-size{
	width: 40rpx;
	height: 40rpx;
	margin-right: 1px;
	border-radius: 50% !important;
	
}

.mg{
	margin: 5px;
}
/* 点赞列表 */
.fabulous-list{
	flex-wrap: wrap;
}
/* 聊天列表 */
.chat-list{
	flex-direction: column;
}
/* 名字 */
.user_name{
	margin-left: 2px;
	line-height: 44rpx;
}
.card-image{
	display: flex;
	flex-wrap: wrap;
	margin-top: 5px;
	
	
}
.image-item > image{
	width: 205rpx;
	height: 205rpx;
	margin: 2px;
	border-radius: 5px;
}

.more{
	margin-top: 5px;
}

/* 评论区样式开始 */
.chat-comment-content{
	margin-top: 10px;
	padding: 10px 0px;
	background-color: white;
	flex: 1;
}
.chat-comment-content > .l_r{
	padding: 0 30rpx;
}
.chat-comment-list{
	padding-top: 30px;
}
</style>